import React from "react";
import {
  Form,
  Select,
  Input,
  Button,
  Row,
  Col,
  Divider,
  Modal,
  notification,
  Tooltip,
  Pagination,
  Image,
  Carousel,
} from "antd";
import axios from "axios";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { FormInstance } from "antd/lib/form";
import { CloudUploadOutlined ,QuestionCircleOutlined } from "@ant-design/icons";
import QuestionList from "./QuestionList";

const Option = Select.Option;
const category = ["","","","","","","","",""];
const openNotification = (type,comment) => {
  notification[type]({
    description: comment,
    placement: "bottomRight",
    duration: 1.5,
    width: "auto",
  });
};

const { TextArea } = Input;

const Question = (props) => {
  const pageSize = parseInt(window.innerHeight / 70);
  //      (height    )
  const department = localStorage.getItem("department");
  const [dataSize,setdataSize] = React.useState(0);
  const [form] = Form.useForm();
  const [visible, setVisible] = React.useState(false);
  const [helpvisible,setHelpVisible] = React.useState(false);
  const [data, setData] = React.useState([]);
  const [page, setPage] = React.useState(0);
  const [dep,setDep] = React.useState([]);
  const token = localStorage.getItem("user_token");
  const header = {
    headers: {
      authorization: `${token}`,
    },
  };

  const loadDep = () => {
    axios
      .get("https://sjswbot.site/dep")
      .then((res) => {
        setDep(res.data.result);
      })
      .catch((error) => {
        console.log("")
      });
  }

  const FormHandler = () => {
    setVisible(true);
  };

  const handleOk = (e) => {
    setVisible(false);
  };

  const handleCancel = (e) => {
    setVisible(false);
  };

  const PageRefresh = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
    // data     

    // return data.map((it, i) => {

    //   return (
    //     <QuestionList
    //       key={i}
    //       data={it}
    //       count={dataSize - i - pageSize * (page)}
    //       getData={getData}
    //       setPage={setPage}
    //       pageSize={pageSize}
    //       page={page}
    //     />
    //   );
    // });
  };

  const onFinishFunc = async (formData) => {
    //  
    for (const [key, value] of Object.entries(formData)) {
      if (value === undefined || value === null || value === NaN) {
        formData[key] = "";
      }
    }

    const response = await axios
      .post(`https://sjswbot.site/knowledgePlus`, formData, header, { widthCredentials:true })
      .then((res) => {
        console.log(res);
        if (res.status === 200) {
          setPage(0);
          getData();
          setVisible(false);
          form.setFieldsValue({
            faqno: "",
            category1: "",
            category2: "",
            category3: "",
            category4: "",
            question: "",
            questionAnswer: "",
            landingUrl: "",
            imageinfo: "",
          });
          return openNotification('success', ' !');
        }
      })
      .catch((error) => {
        return openNotification('error', '  .');
      });
  };

  const onValuesChange = (changedValue, allValue) => {
    console.log(changedValue);
  };

  const onPageChange = (pagenum) => {
    //pagenum 1,2,3,4   .
    setPage(pagenum-1);
  };

  const getData = React.useCallback(async () => {
    const response = await axios.get(`https://sjswbot.site/knowledgePlus?page=${page}&size=${pageSize}`,header,{ widthCredentials: true });
    console.log(response);
    setdataSize(response.data.result.count);
    setData(response.data.result.rows);
  }, [page,setPage]);

  React.useEffect(() => {
    getData();
    loadDep();
  }, [page,setPage]);

  return (
    <div
      style={{
        margin: "3% 10%",
        padding: "1% 0%",
        display: "flex",
        alignItems: "center",
        flexDirection: "column",
        background: "white",
        borderRadius: "0.5rem",
        border: "2px solid lightgray",
      }}
    >
      <div
        style={{
          textAlign: "center",
          fontSize: "30px",
          fontFamily: "Gothic A1",
          width:"100%"
        }}
      >
        <div style={{width:"100%",margin:"1rem 0 1rem"}}>
          +  / 
          <QuestionCircleOutlined  onClick={()=>setHelpVisible(true)} style={{fontSize:"1.5rem",margin:"1rem 5rem 0",float:"right",position:"absolute"}}/>
        </div>

      </div>
            .
      <div style={{ display: "flex", flexDirection: "row", margin: "10px 0 2rem" }}>
        <Link to="/userquestion">
          <u> </u>
        </Link>{" "}
           
      </div>
      <Modal
        centered
        visible={helpvisible}
        onOk={() => setHelpVisible(false)}
        onCancel={() => setHelpVisible(false)}
        width={"auto"}
        height={"auto"}
        footer={null}
      >
        <Carousel
          style={{ width: "65rem", margin: "1rem" }}
        >
          <div>
            <Image
              src="https://user-images.githubusercontent.com/51112542/106609840-5f703680-65a9-11eb-9847-0f54daeb2a6d.png"
              width="100%"
            />
          </div>
          <div>
            <Image
              src="https://user-images.githubusercontent.com/51112542/106609847-60a16380-65a9-11eb-9492-3fc72d2732f3.png"
              width="100%"
            />
          </div>
          <div>
            <Image
              src="https://user-images.githubusercontent.com/51112542/106609851-61d29080-65a9-11eb-98f8-1b625babedd5.png"
              width="100%"
            />
          </div>
          <div>
            <Image
              src="https://user-images.githubusercontent.com/51112542/106609927-7a42ab00-65a9-11eb-80fc-25cbc99e4387.png"
              width="100%"
            />
          </div>
          <div>
            <Image
              src="https://user-images.githubusercontent.com/51112542/106609862-64cd8100-65a9-11eb-805e-c6d963267397.png"
              width="100%"
            />
          </div>
        </Carousel>
      </Modal>
      {data.map((it, i) => {
        return (
        <QuestionList
          key={i}
          data={it}
          count={dataSize - i - pageSize * (page)}
          getData={getData}
          setPage={setPage}
          pageSize={pageSize}
          page={page}
        />
      );
      })}
      <div style={{ marginBottom: "2rem" }} />
      <Pagination
        current={page+1}
        total={dataSize}
        defaultPageSize={pageSize}
        onChange={onPageChange}
        style={{ marginBottom: "1.5rem" }}
      />
      <Button onClick={FormHandler}> </Button>
      <Modal
        title=""
        visible={visible}
        onOk={handleOk}
        onCancel={handleCancel}
        maskClosable={false}
        footer={[null, null]} //ok cancel   
        width="40rem"
      >
        <Form
          form={form}
          onFinish={onFinishFunc}
          onFieldsChange={onValuesChange}
          autoComplete="off"
          style={{ width: "95%", padding: "0 5%" }}
        >
          <Form.Item
            label=""
            name="question"
            rules={[
              {
                required: true,
                message: " ",
              },
            ]}
            required
          >
            <Input />
          </Form.Item>
          <Form.Item
            label=""
            name="questionAnswer"
            rules={[
              {
                required: true,
                message: " ",
              },
            ]}
            required
          >
            <Input.TextArea 
                  initialvalues={data.questionAnswer} 
                  style={{height:"10rem", resize:"none"}}
            />  
          </Form.Item>
          <Form.Item
            label=""
            name="category1"
            rules={[
              {
                required: true,
                message: " ",
              },
            ]}
            required
          >
            <Select>
                {dep.map(i => (i.department === "" || i.idx === Number(department)) ? <Option value={i.department}>{i.department}</Option>:null)}
            </Select>
          </Form.Item>
          <Form.Item 
            label="" 
            name="category2"
            rules={[
              {
                required: true,
                message: " ",
              },
            ]}
            required
            >
              <Select>
                  {category.map(i => <Option value={i}>{i}</Option>)}
              </Select>
          </Form.Item>
          <Form.Item label="" name="category3">
            <Input />
          </Form.Item>
          {/* <Form.Item label=" 2" name="category4">
            <Input />
          </Form.Item>
          <Form.Item label=" 3" name="category5">
            <Input />
          </Form.Item> */}
          <Form.Item label="" name="landingUrl">
            <Input />
          </Form.Item>
          {/* <Form.Item label="" name="imageinfo">
            <Input />
          </Form.Item> */}
          <Form.Item colon={false} wrapperCol={{ offset: 11 }}>
            <Button
              icon={<CloudUploadOutlined />}
              htmlType="submit"
              style={{ margin: "1rem 1rem 0 1rem" }}
            >
              
            </Button>
          </Form.Item>
        </Form>
      </Modal>
    </div>
  );
};

export default Question;
