import React, { useEffect } from "react";
import { Form, Select, Input, Button, Row, Col, notification, Divider, Modal } from "antd";
import axios from "axios";
import styled from "styled-components";
import moment from "moment";
import { FormInstance } from "antd/lib/form";
import {
  CloudUploadOutlined,
  ExclamationCircleOutlined,
} from "@ant-design/icons";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";

const MySwal = withReactContent(Swal);
const openNotification = (type,comment) => {
  notification[type]({
    description: comment,
    placement: "bottomRight",
    duration: 1.5,
    width: "auto",
  });
};

const DataModifylist = ({ data, getData, setPage, page, count ,pageSize}) => {
  const token = localStorage.getItem("user_token");
  const header = {
    headers: {
      authorization: `${token}`,
    },
  };

  const confirmFunc = (formData) => {
    Swal.fire({
      title: "?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "",
      cancelButtonText: "",
    }).then((result) => {
      if (result.isConfirmed) {
        onDeleteFunc(formData);
      }
    });
  };

  const onDeleteFunc = async (formData) => {
    const response = await axios
      .delete(`https://sjswbot.site/fixRequest/${data.idx}`, header, { widthCredentials: true })
      .catch((error) => {
        console.log(error);
        return openNotification('error', '  .');
      });
    setPage(page);
    getData();
    return openNotification('success', ' !');
  };

  return (
    <>
      <Divider style={{ margin: "1vh 0" }} />
      <Row
        justify="start"
        style={{
          width: "90%",
          border: "1px solid lightgray",
          padding: "0.8rem 0",
          margin: "0.1rem 1rem",
          display: "flex",
          alignItems: "center",
          borderRadius: "5px",
        }}
      >
        <Col
          flex={1}
          style={{ paddingLeft: "2rem", width: "5%", fontWeight: "bold" }}
        >
          {count}
        </Col>
        <Col flex={8} style={{ paddingLeft: "2rem", width: "60%" }}>
          {data.question}
        </Col>
        <Col flex={2} style={{ width: "15%" }}>
          {moment(data.updatedAt).format("LLL")}
        </Col>
        <Col flex={1}>
          <Button
            onClick={confirmFunc}
            type="primary"
            style={{ backgroundColor: "RGB(255,0,0,0.6)", border: "none" }}
          >
            
          </Button>
        </Col>
      </Row>
    </>
  );
};

export default DataModifylist;
