import React, { useEffect } from "react";
import { Form, Select, Input, Button, Row, Col, Divider,notification, Modal } from "antd";
import axios from "axios";
import styled from "styled-components";
import { FormInstance } from "antd/lib/form";
import moment from "moment";
import {
  CloudUploadOutlined,
  ExclamationCircleOutlined,
} from "@ant-design/icons";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";

const MySwal = withReactContent(Swal);
const openNotification = (type,comment) => {
  notification[type]({
    description: comment,
    placement: "bottomRight",
    duration: 1.5,
    width: "auto",
  });
};

const Userquestionlist = ({ data, getData, setPage, page }) => {
  const token = localStorage.getItem("user_token");
  const deptname = localStorage.getItem("dept_name");
  const header = {
    headers: {
      authorization: `${token}`,
    },
  };
  const confirmFunc = (formData) => {
    Swal.fire({
      title: "?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "",
      cancelButtonText: "",
    }).then((result) => {
      if (result.isConfirmed) {
        onDeleteFunc(formData);
      }
    });
  };

  const onDeleteFunc = async (formData) => {
    const response = await axios
      .delete(`https://sjswbot.site/question/${data.idx}`,header, {widthCredentials:true})
      .catch((error) => {
        return openNotification('error', '  .');
        console.log(error);
      });
    setPage(page);
    getData();
    return openNotification('success', ' !');
  };

  return (
    <>
      <Divider style={{ margin: "1vh 0" }} />
      <Row
        justify="start"
        style={{
          width: "90%",
          border: "1px solid lightgray",
          padding: "0.8rem 0",
          margin: "0.1rem 1rem",
          display: "flex",
          alignItems: "center",
          borderRadius: "5px",
        }}
      >
        <Col
          flex={1}
          style={{ textAlign: "center", width: "15%", fontWeight: "bold" }}
        >
          {data.department === 11
          ?""
          :deptname}
        </Col>
        <Col flex={8} style={{ marginLeft: "2rem", width: "50%" }}>
          {data.content}
        </Col>
        <Col flex={2} style={{ width: "15%" }}>
          {moment(data.updatedAt).format("LLL")}
        </Col>
        <Col flex={1}>
          <Button
            onClick={confirmFunc}
            type="primary"
            style={{ backgroundColor: "RGB(255,0,0,0.6)", border: "none"}}
          >
            
          </Button>
        </Col>
      </Row>
    </>
  );
};

export default Userquestionlist;
