import React from "react";
import {
  Form,
  Select,
  Input,
  Button,
  Row,
  Col,
  Divider,
  Modal,
  Pagination,
  Image,
  Carousel,
} from "antd";
import { Link } from "react-router-dom";
import axios from "axios";
import styled from "styled-components";
import { FormInstance } from "antd/lib/form";
import { CloudUploadOutlined } from "@ant-design/icons";
import DataModifylist from "./DataModifyList";

const Option = Select.Option;
const { TextArea } = Input;

const DataModify = (props) => {
  const department = localStorage.getItem("department");
  const pageSize = parseInt(window.innerHeight / 70);
  const [dataSize,setdataSize] = React.useState(0);
  let count = 0;
  const [size, setSize] = React.useState(0);
  //      (height    )
  const [form] = Form.useForm();
  const [visible, setVisible] = React.useState(false);
  const [data, setData] = React.useState([]);
  const [page, setPage] = React.useState(0);
  const token = localStorage.getItem("user_token");
  const header = {
    headers: {
      authorization: `${token}`,
    },
  };
  
  const FormHandler = () => {
    setVisible(true);
  };

  const handleOk = (e) => {
    setVisible(false);
  };

  const handleCancel = (e) => {
    setVisible(false);
  };

  const PageRefresh = () => {
    // const _data = data.slice(
    //   (count - 1) * pageSize,
    //   (count - 1) * pageSize + pageSize
    // );
    // data page   

    window.scrollTo({ top: 0, behavior: "smooth" });
    // data     

    return data.map((it, i) => {
      //   

      return (
        <DataModifylist
          key={i}
          data={it}
          count={dataSize - i - pageSize * (page)}
          getData={getData}
          setPage={setPage}
          pageSize={pageSize}
          page={page}
        />
      );
    });
  };

  const onValuesChange = (changedValue, allValue) => {
    console.log(changedValue);
  };

  const onPageChange = (pagenum) => {
    setPage(pagenum-1);
  };

  const getData = React.useCallback(async () => {
    const response = await axios.get(`https://sjswbot.site/fixRequest?page=${page}&size=${pageSize}`, header, { widthCredentials: true });

    console.log(`https://sjswbot.site/fixRequest?page=${page}&size=${pageSize}`);
    setdataSize(response.data.result.count);
    setData(response.data.result.rows);
    PageRefresh();
    // setData(response.data.values.reverse());
  },[page,setPage]);

  React.useEffect(() => {
    getData();
  }, [page,setPage]);

  return (
    <div
      style={{
        margin: "3% 10%",
        padding: "1% 0%",
        display: "flex",
        alignItems: "center",
        flexDirection: "column",
        background: "white",
        borderRadius: "0.5rem",
        border: "2px solid lightgray",
      }}
    >
      <div
        style={{
          textAlign: "center",
          fontSize: "30px",
          fontFamily: "Gothic A1",
        }}
      >
        <p>   </p>
      </div>
           .
      <div style={{ display: "flex", flexDirection: "row", margin: "10px 0 2rem" }}>
        <Link to="/question">
          <u> </u>
        </Link>{" "}
             
      </div>
      {/* {data.map((it,i)=>{
        it.count=data.length-i;
        it.props=props;
        return(
            <QuestionList key = {i} data={it} getData={getData}/>
        )
      })} */}
      {PageRefresh()}
      <div style={{ marginBottom: "2rem" }} />
      <Pagination
        current={page+1}
        total={dataSize}
        defaultPageSize={pageSize}
        onChange={onPageChange}
        style={{ marginBottom: "1.5rem" }}
      />
    </div>
  );
};

export default DataModify;
