import React from "react";
import moment from "moment";
import { Form, Select, Input, Button,notification, Carousel, Image, Divider } from "antd";
import axios from "axios";
import styled from "styled-components";
import { FormInstance } from "antd/lib/form";
import { CloudUploadOutlined } from "@ant-design/icons";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";

const MySwal = withReactContent(Swal);
const openNotification = (type,comment) => {
  notification[type]({
    description: comment,
    placement: "bottomRight",
    duration: 1.5,
    width: "auto",
  });
};

const Option = Select.Option;
const { TextArea } = Input;

const Status = () => {
  const id = localStorage.getItem("username");
  //const [time,setTime] = React.useState(new Date().toLocaleString());
  const [form] = Form.useForm();
  const department = localStorage.getItem("department");
  const deptname = localStorage.getItem("dept_name");
  const [isDisable, setDisable] = React.useState(true);
  const [data, setData] = React.useState({});
  const getData = React.useCallback(async () => {
    const response = await axios.get(`https://sjswbot.site/status/${department}`);
    console.log(response);
    let data = "";
    if (response.data.result.status === 1) {
      data = "";
    } else if (response.data.result.status === 2) {
      data = "";
    }
    setData({
      modifier: response.data.result.User.username,
      time: moment(response.data.result.updatedAt).format("LLL"),
      //    (21-01-21)
    });
    form.setFieldsValue({
      // department: response.data.result.department,
      status: data,
      comment: response.data.result.comment,
      position: response.data.result.position,
      phoneNumber: response.data.result.phoneNumber,
      // time: time,
    });
  });

  const onValuesChange = (changedValue, allValue) => {

  };

  const confirmFunc = (formData) => {
    Swal.fire({
      title: "?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "",
      cancelButtonText: "",
    }).then((result) => {
      if (result.isConfirmed) {
        // setTime(new Date().toLocaleString());
        console.log(formData);
        onFinish(formData);
      }
    });
  };

  const onFinish = async (data) => {
    // data.modifier = localStorage.getItem("username");
    //  modifier   
    const token = localStorage.getItem("user_token");
    const header = {
      headers: {
        authorization: `${token}`,
      },
    };

    if(data.status === ""){
      data.status = 1;
    }
    else if (data.status === ""){
      data.status = 2;
    }
    
    const response = await axios
      .put(`https://sjswbot.site/status/${department}`, data, header, { widthCredentials: true })
      .then((res) => {
        if (res.status === 200) {
          return Swal.fire({
            icon: "success",
            title: " ",
            showConfirmButton: false,
            width: "20rem",
            timer: 1500,
          }).then(()=>getData());
        }
      })
      .catch((err) => {
        return openNotification('error', '  .');
      });
  };

  React.useEffect(() => {
    setDisable(false);
  }, [form.data]);

  React.useEffect(() => {
    console.log(department);
    if (department === "") return;
    getData();
  }, [department]);

  return (
    <div
      style={{
        margin: "3% 10%",
        padding: "1% 0%",
        display: "flex",
        alignItems: "center",
        flexDirection: "column",
        background: "white",
        borderRadius: "0.5rem",
        border: "2px solid lightgray",
      }}
    >
      <div
        style={{
          textAlign: "center",
          fontSize: "30px",
          fontFamily: "Gothic A1",
        }}
      >
        <p>{deptname}   </p>
      </div>
      <Carousel
        style={{ width: "50rem", height: "32rem", margin: "1rem 1rem 2rem" }}
      >
        <div>
          <Image
            src="https://user-images.githubusercontent.com/51112542/98468848-ae633580-221f-11eb-9afd-762834755b54.png"
            width="100%"
          />
        </div>
        <div>
          <Image
            src="https://user-images.githubusercontent.com/51112542/106381844-f3ea6580-63fe-11eb-9c45-c440ca33d15f.png"
            width="100%"
          />
        </div>
        <div>
          <Image
            src="https://user-images.githubusercontent.com/51112542/106381846-f51b9280-63fe-11eb-919d-3f05c9d73e06.png"
            width="100%"
          />
        </div>
        <div>
          <Image
            src="https://user-images.githubusercontent.com/51112542/106381847-f64cbf80-63fe-11eb-833f-6428fd43749e.png"
            width="100%"
          />
        </div>
      </Carousel>
      <Form
        form={form}
        onFinish={confirmFunc}
        onFieldsChange={onValuesChange}
        style={{ width: "40rem" }}
      >
        {/* <Form.Item
          label=""
          name="department"
          value={department}
          style={{
            width: "0rem",
            height: "0rem",
            visibility: "hidden",
            margin: "0",
          }}
        >
          <Input readOnly={true} />
        </Form.Item> */}
        {/* <Form.Item label="" name="time" value={time} style={{width:"0rem", height:"0rem" , visibility:"hidden", margin:"0"}}>
          <Input readOnly="true"/>
        </Form.Item> */}
        {/* <Form.Item
          label=""
          name="modifier"
          value={id}
          style={{
            width: "0rem",
            height: "0rem",
            visibility: "hidden",
            margin: "0",
          }}
        >
          <Input readOnly={true} />
        </Form.Item> */}
        <Form.Item label="" name="position" required>
          <Input />
        </Form.Item>
        <Form.Item label="" name="phoneNumber" required>
          <Input />
        </Form.Item>
        <Form.Item label=" " name="status" required>
          <Select>
            <Option value="1"></Option>
            <Option value="2"></Option>
          </Select>
        </Form.Item>
        <Form.Item
          label=""
          name="comment"
          // rules={[
          //   { required: true, messsage: " " },
          // ]}\
          required
        >
          <TextArea disabled={isDisable} style={{ resize: "none" }} />
        </Form.Item>
        <Divider />
        <p style={{ width: "100%", color: "gray" }}>
          {data.modifier} 
          <br />({data.time})
        </p>
        <Form.Item colon={false} wrapperCol={{ offset: 11 }}>
          <Button icon={<CloudUploadOutlined />} htmlType="submit">
            
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default Status;
