import React, { useState } from "react";
import axios from "axios";
import {
  AutoComplete,
  Form,
  Select,
  Input,
  Divider,
  notification,
  Button,
  Carousel,
  Image,
} from "antd";
import moment from "moment";
import { CloudUploadOutlined } from "@ant-design/icons";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";

const MySwal = withReactContent(Swal);
const openNotification = (type,comment) => {
  notification[type]({
    description: comment,
    placement: "bottomRight",
    duration: 2,
    width: "auto",
  });
};

const BestqaData = ({ data, key }) => {
  const [form] = Form.useForm();
  const confirmFunc = (formData) => {
    Swal.fire({
      title: "?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "",
      cancelButtonText: "",
    }).then((result) => {
      if (result.isConfirmed) {
        onFinishFunc(formData);
      }
    });
  };

  const onFinishFunc = async (formData) => {
    const token = localStorage.getItem("user_token");
    const header = {
      headers: {
        authorization: `${token}`,
      },
    };
    console.log(formData);
    const response = await axios
      .put(`https://sjswbot.site/bestqa/${data.idx}`, formData,header,{ widthCredentials: true })
      .then((res) => {
        if (res.status === 200) {
          Swal.fire({
            icon: "success",
            title: " ",
            showConfirmButton: false,
            width: "20rem",
            timer: 1500,
          });
        } else {
          return openNotification('error', '  .');
        }
      })
      .catch((err) => {
        return openNotification('error', '  .');
      });
  };

  return (
    <div
      style={{
        width: "100%",
        height:"auto",
        margin:"0.5rem 0"
      }}
    >
      <Form
        initialValues={{ id: data.idx, question: data.question }}
        onFinish={confirmFunc}
        autoComplete="off"
        form={form}
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "center",
        }}
      >
        <Form.Item label={`TOP ${data.idx} `} name="question">
          <Input style={{ width: "30vw" }} />
        </Form.Item>
        <Form.Item colon={false} wrapperCol={{ span: 11, offset: 11 }}>
          <Button icon={<CloudUploadOutlined />} htmlType="submit">
            
          </Button>
        </Form.Item>
        <div style={{color: "gray" ,marginLeft:'5rem'}}>
            {data.User.username} 
            <br />({moment(data.updatedAt).format("LLL")})
        </div>
      </Form>
    </div>
  );
};

const Bestqa = () => {
  const [data, setData] = React.useState([]);
  const getData = React.useCallback(async () => {
    const response = await axios.get("https://sjswbot.site/bestqa");
    console.log(response);
    setData(response.data.result);
  }, []);
  React.useEffect(() => {
    getData();
  }, []);
  return (
    <div
      style={{
        margin: "3% 10%",
        padding: "1% 0%",
        display: "flex",
        alignItems: "center",
        flexDirection: "column",
        background: "white",
        borderRadius: "0.5rem",
        border: "2px solid lightgray",
      }}
    >
      <div
        style={{
          textAlign: "center",
          fontSize: "30px",
          fontFamily: "Gothic A1",
        }}
      >
        <p>    </p>
      </div>
      <Carousel
        style={{ width: "50rem", height: "32rem", margin: "1rem 1rem 2rem" }}
      >
        <div>
          <Image
            src="https://user-images.githubusercontent.com/51112542/99047553-b502fb80-25d7-11eb-898a-7b1c46473b0f.png"
            width="100%"
          />
        </div>
        <div>
          <Image
            src="https://user-images.githubusercontent.com/51112542/99047557-b6342880-25d7-11eb-8fcf-7256e6887d65.png"
            width="100%"
          />
        </div>
        <div>
          <Image
            src="https://user-images.githubusercontent.com/51112542/99047561-b7fdec00-25d7-11eb-87a3-9b443cd8be5a.png"
            width="100%"
          />
        </div>
        <div>
          <Image
            src="https://user-images.githubusercontent.com/51112542/106379756-8e43ac80-63f1-11eb-9367-b7cc5cdd97d0.png"
            width="100%"
          />
        </div>
      </Carousel>
      {data.map((it, i) => {
        return <BestqaData key={i} data={it} />;
      })}
    </div>
  );
};

export default Bestqa;
